import { Component, Input, OnInit } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { IApplication } from '@c8y/client';
import { UpdateType } from './apps-to-update-remotes-select.model';
import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { EcosystemService } from '@c8y/ngx-components/ecosystem/shared';

@Component({
  templateUrl: './apps-to-update-remotes-select.component.html'
})
export class AppsToUpdateRemotesSelectComponent implements OnInit {
  @Input() apps: IApplication[];
  @Input() updateType: UpdateType;
  @Input() pluginName: string;
  @Input() appsDisabled: Set<IApplication['id']>;
  filterTerm$: BehaviorSubject<string> = new BehaviorSubject('');
  filteredApps$: Observable<IApplication[]> = new BehaviorSubject([]);
  textConfig: {
    header: string;
    applyButton: string;
  };
  appsToUpdateRemotes: IApplication[] = [];
  result: Promise<IApplication[]> = new Promise((resolve, reject) => {
    this._update = resolve;
    this._cancel = reject;
  });
  private _update: (apps: IApplication[]) => void;
  private _cancel: (reason?: any) => void;

  constructor(private bsModalRef: BsModalRef, private ecosystemService: EcosystemService) {}

  ngOnInit(): void {
    this.filteredApps$ = combineLatest([of(this.apps), this.filterTerm$]).pipe(
      map(([apps, filterTerm]) =>
        filterTerm.trim().length === 0
          ? apps
          : apps.filter((application: IApplication) =>
              this.ecosystemService.filterContainString(application.name, filterTerm)
            )
      )
    );
    this.textConfig =
      this.updateType === 'install'
        ? {
            header: gettext('Select applications to install the plugin to'),
            applyButton: gettext('Install')
          }
        : {
            header: gettext('Select applications to uninstall the plugin from'),
            applyButton: gettext('Uninstall')
          };
  }

  cancel() {
    this.bsModalRef.hide();
    this._cancel();
  }

  setSelectedApps(selected: boolean, app: IApplication) {
    selected
      ? this.appsToUpdateRemotes.push(app)
      : (this.appsToUpdateRemotes = this.appsToUpdateRemotes.filter(
          application => app.key !== application.key
        ));
  }

  async apply() {
    this._update(this.appsToUpdateRemotes);
    this.bsModalRef.hide();
  }
}
