import { Pipe, PipeTransform } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApplicationPlugin } from '@c8y/ngx-components';

@Pipe({
  name: 'onlyLatestPluginVersion'
})
export class OnlyLatestPluginVersionPipe implements PipeTransform {
  transform(
    value: Observable<ApplicationPlugin[]>,
    disabled?: boolean
  ): Observable<ApplicationPlugin[]> {
    if (disabled) {
      return value;
    }

    return value.pipe(map(list => list.filter(plugin => !!plugin.tags?.includes('latest'))));
  }
}
