import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApplicationRemotePlugins, IApplication } from '@c8y/client';
import {
  ActionControl,
  AlertService,
  ApplicationPlugin,
  ApplicationPluginStatus,
  BulkActionControl,
  Column,
  DataGridComponent,
  DisplayOptions,
  GainsightService,
  HeaderActionControl,
  Pagination,
  PluginsService,
  gettext
} from '@c8y/ngx-components';
import { EcosystemService, PRODUCT_EXPERIENCE } from '@c8y/ngx-components/ecosystem/shared';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable, Subject, combineLatest } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { InstallPluginComponent } from './install-plugin.component';
import { LabelCellRendererComponent } from './label-cell-renderer.component';
import { OrphanedStatusCellRendererComponent } from './orphaned-status-cell-renderer.component';
import { UpdatePluginOfAppComponent } from './update-plugin-of-app/update-plugin-of-app.component';

@Component({
  selector: 'c8y-app-plugins',
  templateUrl: './application-plugins.component.html'
})
export class ApplicationPluginsComponent implements OnInit, OnDestroy {
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;
  CURRENT_LOCATION = location.href;

  @Input() appId: string | number;
  @ViewChild(DataGridComponent, { static: false })
  dataGrid: DataGridComponent;

  exportedPlugins$: BehaviorSubject<ApplicationPlugin[]> = new BehaviorSubject([]);
  remotePlugins$: BehaviorSubject<ApplicationRemotePlugins> = new BehaviorSubject({});
  allAvailablePlugins$: BehaviorSubject<ApplicationPlugin[]> = new BehaviorSubject([]);

  installedPlugins$: Observable<ApplicationPlugin[]> = combineLatest([
    this.remotePlugins$.pipe(map(remotes => this.convertInstalledRemotesToIds(remotes))),
    this.allAvailablePlugins$
  ]).pipe(
    map(([remotePlugins, allPlugins]) => this.getInstalledPlugins(allPlugins, remotePlugins)),
    shareReplay(1)
  );
  orphanedPlugins$ = this.installedPlugins$.pipe(
    map(plugins => plugins.filter(p => p.status === ApplicationPluginStatus.ORPHANED))
  );
  app: IApplication;
  isLoading: boolean;
  title: string = gettext('Installed plugins');
  loadMoreItemsLabel: string = gettext('Load more packages');
  loadingItemsLabel: string = gettext('Loading packagesâ€¦');
  actionControls: ActionControl[] = this.getActionControls();
  bulkActionControls: BulkActionControl[] = this.getBulkActionControls();
  headerActionControls: HeaderActionControl[] = [];
  pagination: Pagination = {
    pageSize: 10,
    currentPage: 1
  };
  displayOptions: DisplayOptions = {
    bordered: false,
    striped: true,
    filter: true,
    gridHeader: true
  };
  columns: Column[] = [
    {
      name: 'name',
      header: gettext('Plugin name'),
      path: 'name',
      filterable: true
    },
    {
      name: 'Version',
      header: gettext('Version'),
      path: 'displayVersion',
      filterable: false
    },
    {
      name: 'description',
      header: gettext('Description'),
      path: 'description',
      filterable: false,
      cellCSSClassName: 'small'
    },
    {
      name: 'contextPath',
      header: gettext('Source'),
      path: 'contextPath',
      filterable: false,
      cellRendererComponent: LabelCellRendererComponent
    },
    {
      name: 'status',
      header: gettext('Status'),
      path: 'status',
      filterable: false,
      cellRendererComponent: OrphanedStatusCellRendererComponent
    }
  ];
  private destroy$: Subject<any> = new Subject();

  constructor(
    private activatedRoute: ActivatedRoute,
    private ecosystemService: EcosystemService,
    private bsModalService: BsModalService,
    private pluginsService: PluginsService,
    private alertService: AlertService,
    private gainsightService: GainsightService
  ) {}

  ngOnInit() {
    this.addInstallButtonToHeaderActionControls();
    this.loadData();
  }

  async loadData() {
    this.isLoading = true;
    await this.getApplicationMO();
    await this.getApplicationMFExports(this.app);
    await this.getApplicationMFRemotes(this.app);
    await this.getAllApplicationsMFExports();
    this.isLoading = false;
  }

  async installPlugins() {
    try {
      const pluginsToAdd: ApplicationPlugin[] =
        await this.bsModalService.show<InstallPluginComponent>(InstallPluginComponent, {
          class: 'modal-sm',
          ariaDescribedby: 'modal-body',
          ariaLabelledBy: 'modal-title',
          initialState: this.getInstallModalInitState(),
          ignoreBackdropClick: true
        }).content.result;

      const licensesVerifiedByUser = await this.ecosystemService.verifyLicenses(pluginsToAdd);
      if (!licensesVerifiedByUser) {
        return;
      }
      const verifyVersionCompatibility =
        await this.ecosystemService.verifyPluginVersionsCompatibility(pluginsToAdd, this.app);

      if (!verifyVersionCompatibility) {
        return;
      }

      this.isLoading = true;
      await this.handleRemotesInstallation(pluginsToAdd);

      this.alertService.success(gettext('Plugins installed.'));
      this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.INSTALLED_PLUGINS, {
        component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_PLUGINS,
        result: PRODUCT_EXPERIENCE.APPLICATIONS.RESULTS.PLUGIN_INSTALLED,
        url: this.CURRENT_LOCATION
      });
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
        this.gainsightService.triggerEvent(
          PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.INSTALLED_PLUGINS,
          {
            component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_PLUGINS,
            result: PRODUCT_EXPERIENCE.APPLICATIONS.RESULTS.SERVER_FAILURE,
            url: this.CURRENT_LOCATION
          }
        );
      }
    } finally {
      this.isLoading = false;
    }
  }

  async removePlugins(plugins: string[]) {
    try {
      this.isLoading = true;
      const updatedRemotes = await this.pluginsService.removeRemotes(
        this.app,
        plugins.map(id => ({ id } as ApplicationPlugin))
      );
      this.emitRemotes(updatedRemotes);

      this.isLoading = false;
      this.dataGrid.cancel();
      this.alertService.success(gettext('Plugins removed.'));
      this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.INSTALLED_PLUGINS, {
        component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_PLUGINS,
        result: PRODUCT_EXPERIENCE.APPLICATIONS.RESULTS.PLUGIN_REMOVED,
        url: this.CURRENT_LOCATION
      });
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
        this.gainsightService.triggerEvent(
          PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.INSTALLED_PLUGINS,
          {
            component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_PLUGINS,
            result: PRODUCT_EXPERIENCE.APPLICATIONS.RESULTS.SERVER_FAILURE,
            url: this.CURRENT_LOCATION
          }
        );
      }
    } finally {
      this.isLoading = false;
    }
  }

  async cleanupOrphanedPlugins(plugins: ApplicationPlugin[]) {
    const pluginIds = plugins.map(p => p.id);
    await this.removePlugins(pluginIds);
  }

  getActionControls(): ActionControl[] {
    return [
      {
        type: 'customUpdate',
        text: gettext('Update'),
        icon: 'caret-square-o-up',
        showIf: plugin => {
          return (
            plugin.status === ApplicationPluginStatus.OUTDATED ||
            plugin.status === ApplicationPluginStatus.REVOKED
          );
        },
        callback: plugin => this.updatePlugin(this.app, plugin as ApplicationPlugin)
      },
      {
        type: 'customDowngrade',
        text: gettext('Downgrade'),
        icon: 'caret-square-o-down',
        showIf: plugin => {
          return plugin.status === ApplicationPluginStatus.LATEST;
        },
        callback: plugin => this.updatePlugin(this.app, plugin as ApplicationPlugin, true)
      }
    ];
  }

  getBulkActionControls(): BulkActionControl[] {
    return [
      {
        type: 'customDelete',
        text: gettext('Remove'),
        icon: 'trash',
        callback: plugins => this.removePlugins(plugins)
      }
    ];
  }

  async updatePlugin(app: IApplication, plugin: ApplicationPlugin, downgrade = false) {
    try {
      await (
        this.bsModalService.show(UpdatePluginOfAppComponent, {
          class: 'modal-sm',
          ariaDescribedby: 'modal-body',
          ariaLabelledBy: 'modal-title',
          initialState: {
            app,
            plugin,
            downgrade
          },
          ignoreBackdropClick: true
        }).content as UpdatePluginOfAppComponent
      ).result;
      this.refresh();
    } catch (er) {
      return;
    }
  }

  refresh() {
    this.loadData();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private addInstallButtonToHeaderActionControls() {
    if (this.appId) {
      this.headerActionControls = [
        {
          text: gettext('Install plugins'),
          callback: () => {
            this.installPlugins();
          },
          icon: 'plus-circle',
          type: 'custom'
        }
      ];
    }
  }

  private async handleRemotesInstallation(plugins: ApplicationPlugin[]) {
    // When remotes object is not set in the configuration object of an application.
    // Fallback to setInitialRemotes is triggered.
    let updatedRemotes = await this.pluginsService.addRemotes(this.app, plugins);

    if (!updatedRemotes) {
      // TODO discuss if we need to handle it like that.
      // Right now remotes from the cumulocity.json are taken into account when remotes object is missing in the configuration.
      updatedRemotes = await this.pluginsService.setInitialRemotes(this.app);
      // Fresh application MO is needed, after initial state was set.
      await this.getApplicationMO();
      updatedRemotes = await this.pluginsService.addRemotes(this.app, plugins);
    }
    return this.emitRemotes(updatedRemotes);
  }

  private emitRemotes(updatedRemotes: ApplicationRemotePlugins) {
    this.remotePlugins$.next(updatedRemotes);
    this.app.config.remotes = updatedRemotes;
    return { ...this.remotePlugins$.value };
  }

  private async getApplicationMO(): Promise<IApplication> {
    let id = this.appId;
    if (!id) {
      const { id: routeId } = this.activatedRoute.snapshot.parent.data.contextData;
      id = routeId;
    }

    try {
      this.app = await this.ecosystemService.getApplication(id);
    } catch (er) {
      if (er) {
        this.alertService.addServerFailure(er);
      }
    }
    return this.app;
  }

  private async getApplicationMFExports(app: IApplication) {
    const exports = this.pluginsService.getMFExports(app);
    this.exportedPlugins$.next(exports);
  }

  private async getApplicationMFRemotes(app: IApplication) {
    let appConfigRemotes = this.pluginsService.getMFRemotes(app);

    if (!appConfigRemotes) {
      appConfigRemotes = await this.pluginsService.setInitialRemotes(app);
      this.app = await this.getApplicationMO();
    }
    this.remotePlugins$.next(appConfigRemotes || {});
  }

  private async getAllApplicationsMFExports() {
    const allAppsMFExports = await this.pluginsService.getAllMFExports(true);
    this.allAvailablePlugins$.next(allAppsMFExports);
  }

  private getInstallModalInitState(): any {
    return {
      plugins$: combineLatest([
        this.allAvailablePlugins$,
        this.installedPlugins$.pipe(map(plugins => plugins.map(p => p.id))),
        this.exportedPlugins$.pipe(map(plugins => plugins.map(p => p.id)))
      ]).pipe(
        map(([allPlugins, installedPlugins, exportedPluginNames]) => {
          const plugins = [];
          allPlugins
            .filter(p => !exportedPluginNames.includes(p.id))
            .map(p => plugins.push({ ...p, installed: !!installedPlugins.includes(p.id) }));
          return plugins;
        }),
        shareReplay(1)
      )
    };
  }

  private convertInstalledRemotesToIds(remotes: ApplicationRemotePlugins): string[] {
    if (!remotes) {
      return;
    }
    const importContextPaths = Object.keys(remotes);
    const plugins = [];
    importContextPaths.forEach(contextPath => {
      const moduleNames = remotes[contextPath];
      plugins.push(...moduleNames.map(module => this.createId(contextPath, module)));
    });
    return plugins;
  }

  private createId(contextPath: string, pluginModuleName: string): string {
    return `${contextPath}/${pluginModuleName}`;
  }

  private getOrphanedPlugins(
    orphanedPluginIds: string[],
    allPlugins: ApplicationPlugin[]
  ): ApplicationPlugin[] {
    const orphanedPlugins: ApplicationPlugin[] = orphanedPluginIds.map(p => this.extractDetails(p));
    const orphanedPluginsUpdated = orphanedPlugins.map(p => {
      const pluginInDifferentVersion = allPlugins.find(
        tmp => tmp.contextPath === p.contextPath && tmp.module === p.module
      );
      if (pluginInDifferentVersion) {
        return {
          ...pluginInDifferentVersion,
          version: p.version || pluginInDifferentVersion.version,
          id: p.id,
          status: p.version ? ApplicationPluginStatus.OUTDATED : ApplicationPluginStatus.LATEST
        };
      }
      return p;
    });

    return orphanedPluginsUpdated;
  }

  private splitOrphanedPluginsIntoOrphanedAndRevokedPlugins(
    allPlugins: ApplicationPlugin[],
    orphanedPlugins: ApplicationPlugin[]
  ) {
    const revokedPlugins = new Array<ApplicationPlugin>();
    const actuallyOrphanedPlugins = new Array<ApplicationPlugin>();

    for (const plugin of orphanedPlugins) {
      const foundFamiliarPlugin = allPlugins.find(
        plugin1 => plugin.contextPath === plugin1.contextPath && plugin.module === plugin1.module
      );
      if (foundFamiliarPlugin) {
        revokedPlugins.push(
          Object.assign({}, foundFamiliarPlugin, plugin, {
            status: ApplicationPluginStatus.REVOKED
          })
        );
      } else {
        actuallyOrphanedPlugins.push(plugin);
      }
    }

    return { revokedPlugins, actuallyOrphanedPlugins };
  }

  private getInstalledPlugins(
    allPlugins: ApplicationPlugin[],
    remotePlugins: string[]
  ): ApplicationPlugin[] {
    const availablePlugins = allPlugins
      .filter(plugin => remotePlugins.includes(plugin.id))
      .map(plugin =>
        Object.assign(plugin, {
          status: plugin.tags?.includes('latest')
            ? ApplicationPluginStatus.LATEST
            : ApplicationPluginStatus.OUTDATED
        })
      );
    const orphanedPluginIds = remotePlugins.filter(
      r => !availablePlugins.find(plugin => plugin.id === r)
    );
    const orphanedPlugins: ApplicationPlugin[] = this.getOrphanedPlugins(
      orphanedPluginIds,
      allPlugins
    );

    const { actuallyOrphanedPlugins, revokedPlugins } =
      this.splitOrphanedPluginsIntoOrphanedAndRevokedPlugins(allPlugins, orphanedPlugins);

    return [...availablePlugins, ...revokedPlugins, ...actuallyOrphanedPlugins].map(plugin => ({
      ...plugin,
      // adding 'v' prefix so version string is not treated as date in data grid
      displayVersion: 'v' + plugin.version
    }));
  }

  private extractDetails(pluginId: string): ApplicationPlugin {
    const contextPath = this.getStringMatchingRegex(pluginId, /^[^@]*(@|\/)/);
    const version = this.getStringMatchingRegex(pluginId, /@.*\//);
    const module = this.getStringMatchingRegex(pluginId, /\/.*$/);
    const unavailable = gettext('unavailable`plugin`');
    return {
      id: pluginId,
      path: '',
      module,
      name: module,
      status: ApplicationPluginStatus.ORPHANED,
      contextPath: contextPath,
      description: unavailable,
      version: version
    };
  }

  private getStringMatchingRegex(
    str: string,
    regex: { [Symbol.match](string: string): RegExpMatchArray | null }
  ): string {
    const matches = str.match(regex);
    const value = matches ? matches[0] : '';

    return value.replace(/(@|\/)/g, '');
  }
}
