import { Pipe, PipeTransform } from '@angular/core';
import { IApplication } from '@c8y/client';
import { ApplicationState, EcosystemService } from '@c8y/ngx-components/ecosystem/shared';
@Pipe({
  name: 'appState',
  pure: true
})
export class AppStatePipe implements PipeTransform {
  constructor(private ecosystemService: EcosystemService) {}

  transform(
    app: IApplication,
    arg: keyof ApplicationState
  ): ApplicationState[keyof ApplicationState] {
    const appState = this.ecosystemService.getAppState(app);
    return appState[arg];
  }
}
