import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ApplicationPlugin } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-plugin-list-item',
  templateUrl: './plugin-list-item.component.html'
})
export class PluginListItemComponent {
  @Input() plugin: ApplicationPlugin;
  @Input() selectable: boolean;
  @Output() isItemSelected: EventEmitter<boolean> = new EventEmitter();

  onChange(event) {
    this.isItemSelected.next(event);
  }
}
